	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Who Should You Believe?","date":"25 Feb 2004","slug":"/2004-02-25-who-should-you-believe/","tags":"Y2004"},"html":"<p>Sunday is election day in Hamburg. If you believe the NDR, <a title=\"NDR Online: Infratest dimap Umfrage vom 18.02.2004 - Kopf-an-Kopf-Rennen zwischen CDU und Rot-Grün\" href=\"http://www.ndr.de/ndr/regional/hh/wahl/umfrage/20040218/index.html\">the race is neck and neck</a>. If you believe the Springer press, <a title=\"Umfrage: CDU legt zu, SPD verliert\" href=\"http://www.abendblatt.de/daten/2004/02/21/264972.html\">the CDU has a clear lead</a>. Mayor Beust has <a title=\"NDR Online: Bürgerschaftswahl Hamburg 2004 - Kreuzverhör statt TV-Duell: Von Beust gegen Mirow\" href=\"http://www.ndr.de/ndr/regional/hh/wahl/20040223/beust_absage.html\">cancelled a TV debate</a> with SPD candidate Mirow scheduled for this evening. Spiegel Online thinks Mirow's Kindergarten poster <a title=\"Hamburgs Bürgermeisteranwärter Mirow: Lehrling ohne Zauber - Politik - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/politik/deutschland/0,1518,286799,00.html\">made him look silly</a>. And the best information on the election can be found at <a title=\"Wahl in Hamburg - Hamburger Wahlblog\" href=\"http://interferno.org/wahlinhamburg/\">Hamburger Wahlblog</a>.</p>"},"next":{"fields":{"title":"BBC: Swiss hunt air controller's killer","date":"25 Feb 2004","slug":"/2004-02-25-bbc-swiss-hunt-air-controllers-killer/"}},"previous":{"fields":{"title":"www.schwule-fuer-ole.com","date":"25 Feb 2004","slug":"/2004-02-25-wwwschwule-fuer-olecom/"}}},{"node":{"fields":{"title":"BBC: Swiss hunt air controller's killer","date":"25 Feb 2004","slug":"/2004-02-25-bbc-swiss-hunt-air-controllers-killer/","tags":"Y2004"},"html":"<p><a title=\"Revenge?\" href=\"http://news.bbc.co.uk/2/hi/europe/3486864.stm\">BBC: Swiss hunt air controller's killer</a> Swiss police have launched a hunt for the killer of an air traffic controller who had been on duty at the time of a deadly mid-air collision in 2002.</p>"},"next":{"fields":{"title":"Chicago Boyz: The European \"Mood\"","date":"23 Feb 2004","slug":"/2004-02-23-chicago-boyz-the-european-mood/"}},"previous":{"fields":{"title":"Who Should You Believe?","date":"25 Feb 2004","slug":"/2004-02-25-who-should-you-believe/"}}},{"node":{"fields":{"title":"Chicago Boyz: The European \"Mood\"","date":"23 Feb 2004","slug":"/2004-02-23-chicago-boyz-the-european-mood/","tags":"Y2004"},"html":"<p><a title=\"Europe isn't pragmatic, it's an attitude!\" href=\"http://www.chicagoboyz.net/archives/001811.html\">Chicago Boyz: The European \"Mood\"</a></p>"},"next":{"fields":{"title":"Burningbird: Ralph Nader: Unsafe at any speed","date":"23 Feb 2004","slug":"/2004-02-23-burningbird-ralph-nader-unsafe-at-any-speed/"}},"previous":{"fields":{"title":"BBC: Swiss hunt air controller's killer","date":"25 Feb 2004","slug":"/2004-02-25-bbc-swiss-hunt-air-controllers-killer/"}}}],"pathPrefix":"","first":false,"last":false,"index":522,"pageCount":1101}}

